:host .login-avatar {
  all: unset;
  position: relative;
  cursor: pointer;
}

:host([size='xs']) .login-avatar {
  background-color: var(--mdx-comp-login-button-color-avatar-light-icon-avatar-xsmall-background-default);
  border-color: var(--mdx-comp-login-button-color-avatar-light-icon-avatar-xsmall-border-default);
  border-style: solid;
  border-width: var(--mdx-comp-login-buttons-border-width-buttons-x-small-default);
  border-radius: var(--mdx-comp-login-buttons-border-radius-avatar-x-small-default);
  width: var(--mdx-comp-login-buttons-sizing-avatars-x-small-width);
  height: var(--mdx-comp-login-buttons-sizing-avatars-x-small-height);
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: var(--mdx-comp-login-button-color-avatar-light-icon-avatar-xsmall-background-hover);
    border-color: var(--mdx-comp-login-button-color-avatar-light-icon-avatar-xsmall-border-hover);
  }

  &__icon {
    color: var(--mdx-comp-login-button-color-avatar-light-icon-avatar-xsmall-icon-default);
    height: var(--mdx-comp-login-buttons-sizing-avatars-x-small-icon-height);
    width: var(--mdx-comp-login-buttons-sizing-avatars-x-small-icon-width);

    &:hover {
      color: var(--mdx-comp-login-button-color-avatar-light-icon-avatar-xsmall-icon-hover);
    }
  }

  &__initials {
    font: var(--mdx-comp-login-buttons-font-avatar-x-small-initials-default);
    color: var(--mdx-comp-login-button-color-avatar-light-initials-xsmall-font-default);
  }

  &__notification {
    height: var(--mdx-comp-login-buttons-sizing-avatars-x-small-notification-dot-height);
    width: var(--mdx-comp-login-buttons-sizing-avatars-x-small-notification-dot-width);
    background-color: var(--mdx-comp-login-button-color-avatar-light-initials-xsmall-notification-dot-default);
    border-radius: 100%;
    position: absolute;
    top: 0;
    left: 20px;
    z-index: 1;
  }

  &__image {
    border-radius: var(--mdx-comp-login-buttons-border-radius-avatar-x-small-default);
    width: var(--mdx-comp-login-buttons-sizing-avatars-x-small-width);
    height: var(--mdx-comp-login-buttons-sizing-avatars-x-small-height);
    object-fit: cover;

    &:hover {
      opacity: var(--mdx-comp-login-button-opacity-avatar-light-image-hover);
    }
  }
}

