import { Component, Prop, h } from '@stencil/core';
import { AVATAR_SIZES } from './exports';

@Component({
  tag: 'mdx-login-avatar',
  styleUrl: 'login-avatar.scss',
  shadow: true,
})
export class LoginAvatar {
  @Prop({ reflect: true })
  size: (typeof AVATAR_SIZES)[keyof typeof AVATAR_SIZES] = AVATAR_SIZES.SM;

  @Prop()
  isLoggedIn = false;

  @Prop({ reflect: true })
  dark: boolean;

  @Prop()
  userInitials: string;

  @Prop()
  notification?: boolean;

  @Prop()
  profileImageUrl?: string;

  @Prop()
  profileImageAlt?: string;

  @Prop()
  loginAvatarArialabel!: string;

  render() {
    return (
      <button class="login-avatar" aria-label={this.loginAvatarArialabel}>
        {this.notification && this.isLoggedIn && <span class="login-avatar__notification" />}
        {!this.isLoggedIn ? (
          <mdx-icon-user class="login-avatar__icon" />
        ) : typeof this.profileImageUrl === 'string' && this.profileImageUrl !== '' ? (
          <img src={this.profileImageUrl} class="login-avatar__image" alt={this.profileImageAlt} />
        ) : (
          <span class="login-avatar__initials">{this.userInitials}</span>
        )}
      </button>
    );
  }
}
